.form{
    body {
        background: #0e101c;
        font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
          "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
          sans-serif;
      }
      
      form {
        max-width: 500px;
        margin: 0 auto;
      }
      
      h1 {
        font-weight: 100;
        color: white;
        text-align: center;
        padding-bottom: 10px;
        border-bottom: 1px solid rgb(79, 98, 148);
      }
      
      .form {
        background: #0e101c;
        max-width: 400px;
        margin: 0 auto;
      }
      
      p {
        color: #bf1650;
      }
      
      p::before {
        display: inline;
        content: "⚠ ";
      }
      
      input {
        display: block;
        box-sizing: border-box;
        width: 100%;
        border-radius: 4px;
        border: 1px solid white;
        padding: 10px 15px;
        margin-bottom: 10px;
        font-size: 14px;
      }
      
      label {
        line-height: 2;
        text-align: left;
        display: block;
        margin-bottom: 13px;
        margin-top: 20px;
        color: white;
        font-size: 14px;
        font-weight: 200;
      }
      
      button,
      input[type="submit"] {
        background: #ec5990;
        color: white;
        text-transform: uppercase;
        border: none;
        margin-top: 40px;
        padding: 20px;
        font-size: 16px;
        font-weight: 100;
        letter-spacing: 10px;
      }
      
      button {
        display: block;
        appearance: none;
        border-radius: 4px;
        width: 100%;
      }
      
      input[type="submit"]:hover {
        background: #bf1650;
      }
      
      input[type="button"]:active,
      input[type="submit"]:active {
        transition: 0.3s all;
        transform: translateY(3px);
        border: 1px solid transparent;
        opacity: 0.8;
      }
      
      input:disabled {
        opacity: 0.4;
      }
      
      input[type="button"]:hover {
        transition: 0.3s all;
      }
      
      input[type="button"],
      input[type="submit"] {
        -webkit-appearance: none;
      }
      
      .App {
        max-width: 600px;
        margin: 0 auto;
      }
      
      
}